"use client"

import { useRef, useEffect, useState } from 'react';
import { createNotice } from '@/app/actions';
import QuillEditor from './QuillEditor'; // 새로 만든 에디터 컴포넌트 임포트

interface CreateNoticeModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export default function CreateNoticeModal({ isOpen, onClose }: CreateNoticeModalProps) {
  const modalRef = useRef<HTMLDivElement>(null);
  const formRef = useRef<HTMLFormElement>(null);
  const [content, setContent] = useState(''); // 에디터 내용을 위한 state
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (modalRef.current && !modalRef.current.contains(event.target as Node)) {
        onClose();
      }
    };
    if (isOpen) {
      // 모달 열릴 때 내용 초기화
      setContent('');
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen, onClose]);

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsSubmitting(true);

    const formData = new FormData(event.currentTarget);
    formData.set('content', content); // state의 HTML 내용을 폼 데이터에 추가

    const result = await createNotice(formData);
    
    setIsSubmitting(false);

    if (result.success) {
      onClose();
    } else {
      alert(result.error || '저장에 실패했습니다.');
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
      <div ref={modalRef} className="bg-white rounded-lg p-8 w-full max-w-3xl h-[90vh] flex flex-col">
        <h2 className="text-2xl font-bold mb-6">공지사항 작성</h2>
        <form ref={formRef} onSubmit={handleSubmit} className="flex flex-col flex-grow">
          <div className="mb-4">
            <label htmlFor="title" className="block text-sm font-semibold text-gray-700 mb-2">제목</label>
            <input
              type="text"
              id="title"
              name="title"
              className="w-full border rounded-md py-2 px-3 text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#04AAAB]"
              required
            />
          </div>
          <div className="mb-6 flex-grow flex flex-col">
            <label className="block text-sm font-semibold text-gray-700 mb-2">내용</label>
            <div className="flex-grow h-full">
              <QuillEditor value={content} onChange={setContent} />
            </div>
          </div>
          <div className="flex justify-end gap-4">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 bg-gray-200 text-gray-800 rounded-md hover:bg-gray-300"
              disabled={isSubmitting}
            >
              취소
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-[#04AAAB] text-white rounded-md hover:bg-[#038A8A] disabled:bg-gray-400"
              disabled={isSubmitting}
            >
              {isSubmitting ? '저장 중...' : '저장'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
} 